import React from 'react';
import styled from 'styled-components';
import TypeWriter from "typewriter-effect";

const HomePage = styled.section`
  height: 100vh;
  width: 90vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 40% 60%;
  align-items: center;
  justify-content: center;

    @media (min-width: 768px) {
        grid-template-rows: auto;
        grid-template-columns: 60% 40%;
    }
`

const TextSection = styled.div`
  width: 100%;
  align-self: flex-end;
  
  @media (min-width: 768px) {
    align-self: center;
  }
`

const Rlpz = styled.div`
  font-size: 10vw;
  letter-spacing: 0.2rem;
  color: red;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: red;
  margin-bottom: 20px;
  text-shadow: 2px 2px 1px #464646;

  @media(min-width: 768px) {
    font-size: 8vw;
  }
`

const DevText = styled.p`
  text-transform: uppercase;
  margin-bottom: 20px;
  font-style: oblique;
  
    @media (min-width: 768px) { font-size: 1.8vw; }
`

const TextPortfolio = styled.p`
  color: black;
`
  
const SeePortfolio = styled.a`
  -webkit-text-decoration: transparent;
          text-decoration: transparent;
  color: red;
  border-bottom: 1px dotted red;
`

const ImageSection = styled.div`
  align-self: flex-start;
  justify-self: center;
  width: 30vw;
  -webkit-animation: float 5s ease-in-out infinite;
          animation: float 5s ease-in-out infinite;

    @media (min-width: 768px) {
        align-self: center;
        justify-self: flex-start;
    }
`

const ImageRlpz = styled.img`
  margin-top: 50px;
  width: 30vw;
  -webkit-animation: float 5s ease-in-out infinite;
          animation: float 5s ease-in-out infinite;

  @media (min-width: 768px) {
    margin-top: 0px;
    max-width: 300px;
    margin-left: 5vw;
  }
`

class HeroElement extends React.Component {
  render() {
    return (
        <HomePage>
          <TextSection>
            <Rlpz>
                <TypeWriter onInit={(typewriter) => {
                    typewriter.typeString('<span>Remi Lpz</span>').start().deleteChars(3).typeString('Lopez').start();
                        }} />
            </Rlpz>
            <DevText>Développeur Web Junior</DevText>
            <TextPortfolio>Pour découvrir mon travail, <SeePortfolio href="#portfolio">cliquez ici</SeePortfolio> !</TextPortfolio>
          </TextSection>
          <ImageSection>
            <ImageRlpz src={process.env.PUBLIC_URL + '../img/illu-remi.png'} alt="Portrait" />
          </ImageSection>
        </HomePage> 
    )
  }
}

export default HeroElement